* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Pretendard', system-ui, -apple-system, sans-serif;
  font-weight: 500;
}

li {
  list-style: none;
}

button {
  border: none;
  background: none;
  outline: none;
  font-family: inherit;
  cursor: pointer;
}

input,
select {
  font-family: inherit;
}

h1,
h2 {
  font-family: 'NPS';
  font-weight: 500;
}

a {
  text-decoration: none;
  color: inherit;
}