/* 탭 바 */
.pf-event-tabs {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 16px;
  margin: 18px 0 22px;
}

.pf-event-tab-left {
  display: inline-flex;
  gap: 0;
  margin: 20px 0 18px;
  border-radius: 999px;
  padding: 3px;
  border: 1px solid var(--color-border);
  background: #ffffff;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.08);
}

.pf-event-tab-btn {
  appearance: none;
  border: 0;
  background: transparent;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 999px;
  font-size: 14px;
  font-weight: 800;
  color: var(--color-list-title, #333);
  transition: transform 0.08s ease, background 0.15s ease, color 0.15s ease;
}

.pf-event-tab-btn:hover {
  background: rgba(58, 142, 135, 0.08);
}

.pf-event-tab-btn:active {
  transform: scale(0.98);
}

.pf-event-tab-btn.is-active {
  background: var(--color-header-bg, #3a8e87);
  color: var(--color-text, #fff);
}

.pf-event-sep {
  color: var(--color-border, #e8e2d0);
  font-size: 13px;
  user-select: none;
}

.pf-event-count {
  margin: 0;
  font-size: 13px;
  color: var(--color-list-sub, #666);
  line-height: 1.4;
  white-space: nowrap;
}

/* 내용 영역 */
.pf-event-content {
  margin-top: 8px;
}

/* 빈 상태 */
.pf-event-empty {
  margin: 24px 0 0;
  padding: 20px;
  border: 1px dashed var(--color-border, #e8e2d0);
  border-radius: 14px;
  background: #fff;
  color: var(--color-list-sub, #666);
  text-align: center;
}

/* ===== 이벤트 ===== */
.pf-event-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.pf-event-card-link {
  text-decoration: none;
  color: inherit;
  display: block;
}

.pf-event-card {
  position: relative; /* ✅ 오버레이 기준 */
  border: 1px solid var(--color-border, #e8e2d0);
  background: #fff;
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 10px 22px var(--color-shadow-mid, rgba(0, 0, 0, 0.1));
  transition: transform 0.16s ease, box-shadow 0.16s ease, border-color 0.16s ease;
}

.pf-event-card-link:hover .pf-event-card {
  transform: translateY(-3px);
  box-shadow: 0 14px 30px var(--color-shadow-strong, rgba(0, 0, 0, 0.18));
  border-color: rgba(58, 142, 135, 0.35);
}

/* 배너 */
.pf-event-banner {
  position: relative;
  height: 140px;
  background: var(--color-card-bg, #eaeaea);
}

.pf-event-banner-img {
  width: 100%;
  /* height: 100%; */
  object-fit: cover;
  display: block;
}

/* 카드 바디 */
.pf-event-body {
  padding: 14px 16px 14px;
}

.pf-event-card-title {
  margin: 0 0 6px;
  font-size: 16px;
  font-weight: 900;
  color: var(--color-list-title, #222);
  line-height: 1.25;
}

.pf-event-card-desc {
  margin: 0 0 8px;
  font-size: 13px;
  color: var(--color-list-sub, #666);
  line-height: 1.45;
}

.pf-event-card-date {
  margin: 0;
  font-size: 12px;
  color: var(--color-list-sub, #777);
  opacity: 0.95;
}

/* ✅ 이벤트 종료 오버레이: 카드 전체 덮기 */
.pf-event-closed-overlay {
  position: absolute;
  inset: 0;
  z-index: 10;
  background: rgba(10, 10, 10, 0.55);
  color: #fff;
  display: grid;
  place-items: center;
  font-weight: 900;
  letter-spacing: -0.02em;
  font-size: 15px;
  border-radius: 18px;
  pointer-events: none; /* 클릭 막고 싶으면 none 제거 */
}

/* 종료 카드 톤다운 */
.pf-event-card.is-closed {
  opacity: 0.92;
}

/* ===== 공지사항(아코디언) ===== */
.pf-notice-list {
  list-style: none;
  margin: 0;
  padding: 0;
  border-radius: 16px;
  overflow: hidden;
  border: 1px solid var(--color-border, #e8e2d0);
  background: #fff;
  box-shadow: 0 10px 22px var(--color-shadow-mid, rgba(0, 0, 0, 0.1));
}

.pf-notice-item {
  border-bottom: 1px solid rgba(232, 226, 208, 0.8);
}

.pf-notice-item:last-child {
  border-bottom: 0;
}

.pf-notice-head {
  width: 100%;
  background: none;
  border: 0;
  padding: 14px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 14px;
  cursor: pointer;
  text-align: left;
}

.pf-notice-item:hover .pf-notice-head {
  background: rgba(58, 142, 135, 0.06);
}

.pf-notice-main {
  display: flex;
  align-items: center;
  gap: 10px;
  min-width: 0;
}

.pf-notice-tag {
  display: inline-flex;
  align-items: center;
  height: 24px;
  padding: 0 10px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 900;
  color: var(--color-header-bg, #3a8e87);
  background: rgba(58, 142, 135, 0.1);
  border: 1px solid rgba(58, 142, 135, 0.2);
  flex: 0 0 auto;
}

.pf-notice-title {
  font-size: 14px;
  font-weight: 800;
  color: var(--color-list-title, #222);
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.pf-notice-right {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  flex: 0 0 auto;
}

.pf-notice-date {
  font-size: 12px;
  color: var(--color-list-sub, #777);
  white-space: nowrap;
}

.pf-notice-arrow {
  font-size: 14px;
  color: var(--color-list-sub, #777);
  transform: rotate(0deg);
  transition: transform 0.18s ease;
  user-select: none;
}

.pf-notice-arrow.is-open {
  transform: rotate(180deg);
}

/* 아코디언 바디 */
.pf-notice-body {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transform: translateY(-4px);
  transition: max-height 0.22s ease, opacity 0.18s ease, transform 0.18s ease;
  background: rgba(58, 142, 135, 0.04);
  border-top: 1px solid rgba(232, 226, 208, 0.7);
}

.pf-notice-item.is-open .pf-notice-body {
  max-height: 260px;
  opacity: 1;
  transform: translateY(0);
}

.pf-notice-content {
  margin: 0;
  padding: 14px 16px;
  font-size: 13px;
  line-height: 1.6;
  color: var(--color-list-sub, #555);
}
